$hamburger-speed: 1s;

/* Extra small devices (phones, 600px and down), are the default styles ;) */
$s-devices: 600px;  /* Small devices (portrait tablets and large phones, 600px and up) */
$m-devices: 768px;  /* Medium devices (landscape tablets, 768px and up) */
$l-devices: 992px;  /* Large devices (laptops/desktops, 992px and up) */
$xl-devices: 1200px;  /* Extra large devices (large laptops and desktops, 1200px and up) */

.content-wrap {
  padding: 5%;
}

.main-nav {
  width: 100vw;
  height: 12vh;
  position: fixed;
  top:0;
  left: 0;
  background: white;

  // ICON
  &__wrap-hamburger {
    width: 100vw;
    height: 12vh;
    padding: 0.5em 1em 0 1em;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 100%;
    justify-content: space-between;
    align-items: center;
  }

  //LOGO 
  &__logo {
    height: 7vh;
    img {height: 100%;}
  }

  //Hamburger
  &__hamburger {
    display: block;
    grid-column: 12;
    width: 26px;
    height: 16px;
    position: relative;
    margin-right: 3%;
    z-index: 2;
  
    i{
      transition: $hamburger-speed;
      display: block;
      position: absolute;
      width: 26px;
      height: 2px;
      background-color: #161616;
      border-radius: 5px;
    }

    i:nth-of-type(1) {
      transition: $hamburger-speed;
      top: 16px;
    }    
  }

  .nav-collapse {
    background: #161616;
    position: fixed;
    width: 80vw;
    height: 100vh;
    top: 0;
    right: -80vw;
    transition: $hamburger-speed;
    z-index: 1;
    display: grid;
    grid-template-rows: 12vh repeat(11, 1fr);
    grid-template-columns: repeat(12, 1fr);
    
    &__list{
      list-style: none;
      padding: 0;
      margin: 0;
      grid-row-start: 3;
      color: white;
      grid-column: 3 / span 7;
      grid-row: 3 / span 7;
    }

    &__item{
      margin-bottom: 2.3em;
    }

    &__icons {
      grid-column: 3 / span 4;
      grid-row: 11 / auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      img { width: 2.3em}
    }
  }
}


//Styles for when Hamburger Menu is opened
.showMenu { 
  .main-nav__hamburger {
    i {
      -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
      -moz-transform: rotate(-45deg);
      transform: rotate(-45deg);
      top:8px;
      background-color: white;
    }
    
    i:nth-of-type(1) {
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      transform: rotate(45deg);
      top:8px;
    }
  }

  .nav-collapse {
    right: 0;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: $m-devices) {
  .main-nav {
    &__hamburger {
      display: none;
    }

    .nav-collapse {
      width: 85vw;
      height: 12vh;
      right: 0;
      background: white;

      &__list{
        color: black;
        grid-column: 1 / span 11;
        grid-row: 1 / span 1;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
      }

      &__item{
        margin-bottom: 1em;
      }

      &__icons {
        display: none;
      }
    }
  }
}
